.SearchForm {
  display: flex;
  align-items: center;
  max-width: 600px;
  overflow: hidden;
  margin-top: 10px;
}

.SearchForm_button {
  display: inline-block;
  min-width: 50px;
  height: 36px;
  border: 0;
  border-radius: 5px;
  background-size: 40%;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  outline: none;
}

.SearchForm_input {
  width: 400px;
  height: 30px;
  font: inherit;
  font-size: 18px;
  padding-left: 4px;
  padding-right: 4px;
  margin-right: 10px;
  margin-left: 10px;
}

.SearchForm-input::placeholder {
  font: inherit;
  font-size: 18px;
}
